@import url(https://fonts.googleapis.com/css?family=Cormorant+SC|Libre+Baskerville|Montserrat);
body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  background-color: black;
}

.App {
  position: relative;
  color: white;
  padding: 0 120px;
}

.bg {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background-size: contain;
  z-index: -1;
}

.navBar {
  padding: 30px 0px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navBar img {
  width: 100px;
  cursor: pointer;
}

.navMenu {
  display: flex;
  flex-direction: row;
}

.navMenu * {
  margin-left: 30px;
  cursor: pointer;
}

.navMenu img {
  width: 20px;
}

.mainHeader {
  margin-top: 120px;
}

.mainHeader h1 {
  font-size: 100px;
  font-family: 'Cormorant SC', serif;
  font-weight: 300;
  margin: 0;
}

.mainBottomPart {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 100px;
}

.popularList p h3 {
  margin: 10px 0;
}

.popularList p {
  opacity: .7;
  font-size: 13px;
}

.storyArticle {
  width: 90%;
  max-width: 400px;
  font-weight: bold;
}

.storyArticle p {
  font-weight: 100;
  color: rgba(255, 255, 255, 0.849);
}

.underlineParagraph {
    position: relative;
    cursor: pointer;
    display: table;
}

.underlineParagraph:before {
    content: " ";
    position: absolute;
    left: 0;
    bottom: -5px;
    width: 0%;
    height: 3px;
    transition: width 250ms;
    background-color: rgb(87, 173, 244);
}

.underlineParagraph:hover:before {
    width: 100%;
}   
body {
    background-color: black;
}

h1 {
  font-family: 'Cormorant SC', serif;
  font-size: 80px;
  font-weight: 300;
  margin-bottom: 0;
}

.bg {
    background-size: contain;
}

.characterInfo {
    width: 90%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.characterInfo > ul{
    list-style-type: none;
}

.characterInfo li {
    margin-top: 10px;
    color: rgba(255, 255, 255, 0.8);
    list-style-type: none;
    font-weight: 100;
}

.characterInfo span {
    font-size: 25px;
    font-weight: bold;
    font-family: 'Cormorant SC', serif;
    color: white;
}
